import React from 'react';
import './Timeline.css';
import CustomCard from '../../Components/CustomCard/CustomCard';

export default class Timeline extends React.Component {
    render() {
        return (
            <div className="timeline-container">
                <div className="timeline">
                    <div><span className="skills-header">My Career Timeline:</span></div><br />
                    <ul>
                        <li>
                            <div className="content" data-aos="fade-right" data-aos-delay="150">
                                <CustomCard cardHeading={<h3>Class X</h3>} cardBody={
                                    <div>
                                        <h4>... - March 2012</h4>
                                        <p>
                                            This is the time I finished my Class X.<br />
                                            My CGPA is 9.0
                                        </p>
                                    </div>
                                } shadow="5" />
                            </div>
                        </li>
                        <li>
                            <div className="content" data-aos="fade-left" data-aos-delay="150">
                                <CustomCard cardHeading={<h3>Class XII</h3>} cardBody={
                                    <div>
                                        <div>
                                            <h4>June 2012 - April 2014</h4>
                                        </div>
                                        <p>
                                            This is the duration of class XI and class XII.<br />
                                    My precentage is 93.7% <span className="gold-highlight">and I was one in the top 10% of my campus !</span><br />
                                        </p>
                                    </div>
                                } shadow="5" />
                            </div>
                        </li>
                        <li>
                            <div className="content" data-aos="fade-right" data-aos-delay="150">
                                <CustomCard cardHeading={<h3>Graduation</h3>} cardBody={
                                    <div>
                                        <div>
                                            <h4>Bachelor's Degree - June 2014 - June 2018</h4>
                                        </div>
                                        <p>
                                            I have a major in Electronics and Communication. <br />
                                    My aggregate is 80.3%<br />
                                    I have been in <span className="gold-highlight">Top 3 of my Department !</span><br />
                                        </p>
                                    </div>
                                } shadow="5" />
                            </div>
                        </li>
                        <li>
                            <div className="content" data-aos="fade-left" data-aos-delay="150">
                                <CustomCard cardHeading={<h3>Engineering Intern</h3>} cardBody={
                                    <div>
                                        <div>
                                            <h4>OpenText - July 2018 - November 2018</h4>
                                        </div>
                                        <p>
                                            - Worked on creating Desktop Application for Email Consistency and Validation.<br />
                                    - Worked on creating a desktop Dashboard for application maintenance.
                                </p>
                                    </div>
                                } shadow="5" />
                            </div>
                        </li>
                        <li>
                            <div className="content" data-aos="fade-right" data-aos-delay="150">
                                <CustomCard cardHeading={<h3>Application Developer</h3>} cardBody={
                                    <div>
                                        <div>
                                            <h4>Accenture - December 2018 - Present</h4>
                                        </div>
                                        <p>
                                            - Working on React and ASP.Net Web API based Web Application for
                                    Automated deployment and maintenance.<br />
                                    - Worked on creating desktop application for automated deployment.
                                </p>
                                    </div>
                                } shadow="5" />
                            </div>
                        </li>
                        <div style={{ clear: "both" }}>&nbsp;</div>
                    </ul>
                </div>
            </div >
        );
    }
}