import React from 'react';
import './Skills.css';
import CustomCard from '../../Components/CustomCard/CustomCard';

export default class Skills extends React.Component {
    render() {
        return (
            <div className="skills-container">
                <div>
                    |&nbsp;&nbsp;&nbsp;<br />
                    |&nbsp;&nbsp;&nbsp;<br />
                    |&nbsp;&nbsp;&nbsp;<br />
                    <span className="skills-header">
                        &nbsp;&nbsp;Skills&nbsp;&nbsp;</span><br />
                    |&nbsp;&nbsp;&nbsp;<br />
                    |&nbsp;&nbsp;&nbsp;<br />
                    |&nbsp;&nbsp;&nbsp;<br />
                </div>
                <span className="skills-web">
                    <CustomCard direction="left" cardHeading="Web Development" cardBody=
                        {
                            <ul style={{ listStyle: "disc" }}>
                                <span className="blue-highlight">
                                    <li>ASP.NET Web API</li>
                                    <li>React JS</li>
                                    <li>Html CSS JS</li>
                                    <li>SQL Server</li>
                                </span>
                            </ul>
                        }
                        shadow="5" />
                </span>
                <span className="skills-desktop">
                    <CustomCard direction="up" cardHeading="Desktop Development" cardBody=
                        {
                            <ul style={{ listStyle: "disc" }}>
                                <span className="green-highlight">
                                    <li>Window Presentation Foundation</li>
                                    <li>C#.Net</li>
                                    <li>Model-View-ViewModel with Xaml</li>
                                    <li>SQLite</li>
                                </span>
                            </ul>
                        }
                        shadow="5" />
                </span>
                <span className="skills-ds">
                    <CustomCard direction="right" cardHeading="Data Science" cardBody=
                        {
                            <ul style={{ listStyle: "disc" }}>
                                <span className="gold-highlight">
                                    <li>Data Analysis</li>
                                    <li>Data Visualization</li>
                                    <li>Data Interpretation</li>
                                    <li>Recommendation Systems</li>
                                    <li>Machine Learning</li>
                                </span>
                            </ul>
                        }
                        shadow="5" />
                </span>
            </div>
        );
    }
}