import React from 'react';
import './AboutMe.css';
import ProfilePic from '../../Images/profile.png';

export default class AboutMe extends React.Component {
    render() {
        return (
            <div className="aboutme-container">
                <div className="aboutme-desc" data-aos="fade-right" data-aos-delay="300">
                    <p>
                        |<br />
                        |<br />
                        |<br />
                        |<br />
                        |<br />
                        |<br />
                        Hi There.&nbsp;&nbsp;&nbsp;&nbsp;|<br />
                        My name is Venkatesh Kocherlakota.&nbsp;&nbsp;&nbsp;&nbsp;|<br />
                        I live in Bangalore, the Silicon Valley of India.&nbsp;&nbsp;&nbsp;&nbsp;|<br />
                        I m a <span className="blue-highlight">Software Developer</span> skilled in various disciplines.&nbsp;&nbsp;&nbsp;&nbsp;|<br />
                        I develop <span className="blue-highlight">Web and Desktop Applications.</span>&nbsp;&nbsp;&nbsp;&nbsp;|<br />
                        I m also <span className="green-highlight">certified by IBM in Data Science.</span>&nbsp;&nbsp;&nbsp;&nbsp;|<br />
                        <br />
                        Lets look at my skillset below.&nbsp;&nbsp;&nbsp;&nbsp;|<br />
                        |<br />
                        |<br />
                        |<br />
                        |<br />
                        |<br />
                    </p>
                </div>
                <div className="aboutme-profpic" data-aos-delay="300" data-aos="fade-left">
                    <img className="aboutme-profpic-img"
                        src={ProfilePic} alt="venks here" />
                </div>
            </div >
        );
    }
}